import React, { useState, useEffect } from 'react'
import { FormGroup , Form, Input } from 'reactstrap'
import { Link } from 'react-router-dom'
import axios from 'axios';
import { StyleInput, StyleP, StyleButton } from './style';

function Update() {
    const [firstName, setFirstName] = useState('');
    const [lastName, setLastName] = useState('');
    const [checkbox, setCheckbox] = useState(false);
    const [id, setID] = useState(null);

    useEffect(() => {
            setID(localStorage.getItem('ID'))
            setFirstName(localStorage.getItem('First Name'));
            setLastName(localStorage.getItem('Last Name'));
            setCheckbox(localStorage.getItem('Checkbox Value'))
    }, []);
    const updateAPIData = () => {
        axios.put(`https://63517ef6dfe45bbd55c1cdd9.mockapi.io/user/users/${id}`, {
            firstName,
            lastName,
            checkbox
        })
    }
    return (
        <Form className='create-form'>
            <FormGroup>
                <StyleP>FirstName</StyleP><br />
                <StyleInput
                    id="FirstName"
                    name="FirstName"
                    placeholder="FirstName"
                    type="text"
                    value={firstName}
                    onChange={(e) => setFirstName(e.target.value)}
                />
            </FormGroup>

            <FormGroup>
                <StyleP>LastName Password</StyleP><br />
                <StyleInput
                    id="LastName"
                    name="LastName"
                    placeholder="LastName"
                    type="text"
                    value={lastName}
                    onChange={(e) => setLastName(e.target.value)}
                />
            </FormGroup>
            <FormGroup check>
                <Input type="checkbox" className="input3" onChange={(e) => setCheckbox(!checkbox)} />
            </FormGroup>
            <Link to="/read"><StyleButton type="submit" onClick={updateAPIData}>Submit</StyleButton></Link>
        </Form>
    )
}

export default Update
