import React, { useState } from 'react'
import { FormGroup, Form, Input } from 'reactstrap'
import { Link } from 'react-router-dom'
import axios from 'axios';
import { StyleInput, StyleP, StyleButton } from './style';
const Create = () => {
  const [firstName, setFirstName] = useState('')
  const [lastName, setLastName] = useState('')
  const [checkbox, setCheckbox] = useState(false)
  const postData = () => {
    axios.post(`https://63517ef6dfe45bbd55c1cdd9.mockapi.io/user/users`, {
      firstName,
      lastName,
      checkbox,
    })
  }
  return (
    <Form>
      <FormGroup>
        <StyleP>FirstName</StyleP>
        <br />
        <StyleInput
          id="FirstName"
          name="FirstName"
          placeholder="FirstName"
          type="text"
          onChange={(e) => setFirstName(e.target.value)}
        />
      </FormGroup>

      <FormGroup>
        <StyleP >LastName Password</StyleP>
        <br />
        <StyleInput
          id="LastName"
          name="LastName"
          placeholder="LastName"
          type="text"
          onChange={(e) => setLastName(e.target.value)}
        />
      </FormGroup>
      <FormGroup check>
        <Input style={{margin: '10px 0 10px 0', width: '15px', height:'15px'}}
          type="checkbox"
          className="input3"
          onChange={(e) => setCheckbox(!checkbox)}
        />
      </FormGroup>
      <Link to="/read">
        <StyleButton type="submit" onClick={postData}>
          Submit
        </StyleButton>
      </Link>
    </Form>
  )
}

export default Create;