import React, { useEffect, useState } from 'react'
import { Button } from 'reactstrap'
import { Link } from 'react-router-dom';
import axios from 'axios';

function Read() {
    const [APIData, setAPIData] = useState([]);
    useEffect(() => {
        axios.get(`https://63517ef6dfe45bbd55c1cdd9.mockapi.io/user/users`)
            .then((response) => {
                setAPIData(response.data);
            })
    }, [])
    const getData = () => {
        axios.get(`https://63517ef6dfe45bbd55c1cdd9.mockapi.io/user/users`)
            .then((getData) => {
                 setAPIData(getData.data);
             })
    }
    const onDelete = (id) => {
        axios.delete(`https://63517ef6dfe45bbd55c1cdd9.mockapi.io/user/users/${id}`)
        .then(() => {
            getData();
        })
    }
    const setData = (data) => {
        let { id, firstName, lastName, checkbox } = data;
        localStorage.setItem('ID', id);
        localStorage.setItem('First Name', firstName);
        localStorage.setItem('Last Name', lastName);
        localStorage.setItem('Checkbox Value', checkbox);
    }
    return (
        <div>
            <table border="1px" style={{background: 'grey' , textAlign: 'center'}}>
                <tr>
                    <td>ID</td>
                    <td>First Name</td>
                    <td>Last Name</td>
                    <td>Checked</td>
                    <td>Tahrirlash</td>
                    <td>O'chirish</td>
                </tr>
                {
                    APIData.map((data) => {
                        return(
                            <tr key={data.id}>
                                <td >{data.id}</td>
                                <td>{data.firstName}</td>
                                <td>{data.lastName}</td>
                                <td>{data.checkbox ? 'Checked' : 'unChecked'}</td>
                                <td>
                                    <Link to='/update'>
                                        <Button onClick={() => setData(data)}>Update</Button>
                                    </Link>
                                </td>
                                <td><Link to='/read'><Button onClick={() => onDelete(data.id)}>Delete</Button></Link></td>
                            </tr>
                        )
                    })
                }
                
            </table>
        </div>
    )
}

export default Read;
